* {
  margin: 0;
  padding: 0;
 
          box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
}

a {
  text-decoration: none;
  color: black;
}

ul {
  list-style-type: none;
}



h1 {
  margin: 10px;
  text-align: center;
  color: blue;
}

.pictures {
 
  display: -ms-flexbox;
  display: flex;
 
      flex-wrap: wrap;
  
          justify-content: center;
  
          align-items: center;
}

.pictures .picture {
  font-size: 1.2rem;
  padding: 1rem;
  width: 300px;
}

.pictures .picture .imageContainer {
  overflow: hidden;
  height: 40vh;
}

.pictures .picture .imageContainer img {
  width: 100%;
  height: 100%;
  
     object-fit: cover;
  
  transition: all 0.3s ease;
}

.pictures .picture .imageContainer img:hover {
 
          transform: scale(1.25);
}

.pictures .picture .info {
 
  display: flex;
 
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
}
